@import url("https://fonts.googleapis.com/css2?family=Red+Hat+Display:wght@400;500;700;900&display=swap");
*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
}

body {
  box-sizing: border-box;
  font-family: "Red Hat Display", sans-serif;
  font-size: 1.8rem;
  font-weight: 500;
  background-color: var(--background-white);
  max-width: 144rem;
  margin: 0 auto;
}

main {
  overflow: hidden;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

:root {
  --primary: hsl(192, 37%, 48%);
  --secondary: hsl(268, 34%, 53%);
  --primary-light: hsla(192, 54%, 64%, 1);
  --primary-light2: hsl(192, 90%, 77%);
  --secondary-light: hsla(267, 54%, 71%, 1);
  --secondary-light2: hsl(268, 100%, 86%);
  --heading-black-blue: hsl(240, 21%, 20%);
  --text-gray-blue: hsl(240, 10%, 57%);
  --heading-black-blue-light: hsla(239, 21%, 20%, 1);
  --text-gray-blue-light: hsla(240, 10%, 57%, 0.25);
  --text-gray-blue-light2: hsla(239, 10%, 57%, 1);
  --background-white: hsl(0, 0%, 95%);
  --white: hsl(0, 0%, 98%);
}

/*
  0 - 600px:       Phone
  600 - 900px:     Tablet portrait
  900 - 1200px:    Tablet landscape
  [1200 - 1800] is where our normal styles apply. > Desktop frst aprouch
  1800px + :       Big desktop
*/
.heading-primary {
  font-size: 6.4rem;
  font-weight: 900;
  line-height: 6.4rem;
  color: var(--heading-black-blue);
}
@media (max-width: 48em) {
  .heading-primary {
    font-size: 4.8rem;
    line-height: 4.8rem;
  }
}

.heading-secondary {
  font-size: 4rem;
  font-weight: 900;
  line-height: 4.4rem;
  color: var(--heading-black-blue-light);
}
.heading-secondary--footer {
  color: var(--white);
}
@media (max-width: 48em) {
  .heading-secondary--footer {
    text-align: center;
    padding: 0 4rem;
    margin-bottom: 3.2rem;
  }
}

.subheading {
  font-size: 1.6rem;
  text-transform: uppercase;
  font-weight: 900;
  line-height: 2.6rem;
  letter-spacing: 0.4rem;
  color: var(--primary);
}

.footer .btn-download-purple, .hero .btn-download {
  display: inline-block;
  padding: 1.6rem 2.9rem;
  border: none;
  border-radius: 2.8rem;
  cursor: pointer;
  font-size: 1.6rem;
  font-weight: 900;
  color: var(--white);
}

.hero .btn-nav:link,
.hero .btn-nav:visited {
  display: inline-block;
  padding: 1.6rem 2.9rem;
  border-radius: 2.8rem;
  cursor: pointer;
  font-size: 1.6rem;
  font-weight: 900;
  color: var(--white);
}

:is(.footer .btn-download-purple, .hero .btn-download, .hero .btn-nav) {
  transition: all 300ms ease-out;
}

.move-left {
  transform: translateX(-3.2rem);
}
@media (max-width: 48em) {
  .move-left {
    transform: translateX(-2.6rem);
  }
}

.move-right {
  transform: translateX(3.2rem);
}
@media (max-width: 48em) {
  .move-right {
    grid-column: 2/3;
    grid-row: 1/2;
    transform: translateX(0.65rem);
  }
}

.move-up {
  transform: translateY(-50%);
}
@media (max-width: 48em) {
  .move-up--tab {
    transform: translateY(-50%);
  }
}

.move-down {
  transform: translateY(50%);
}
@media (max-width: 48em) {
  .move-down {
    transform: translateY(0);
  }
}

.logo-container {
  display: flex;
  justify-content: center;
  margin-top: 8rem;
}
@media (max-width: 48em) {
  .logo-container {
    margin-top: 4.8rem;
  }
}

.section-hero {
  margin-top: 10.8rem;
}
@media (max-width: 48em) {
  .section-hero {
    margin-top: 11.9rem;
  }
}

.hero {
  display: flex;
}
@media (max-width: 48em) {
  .hero {
    display: grid;
    grid-template-columns: repeat(2, 2fr);
    row-gap: 7.2rem;
  }
}
.hero__img-faces {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 3.2rem;
  row-gap: 2.8rem;
}
.hero__img-faces-face {
  width: 110px;
  height: 110px;
  object-fit: cover;
  border-radius: 100%;
}
.hero__text-box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
@media (max-width: 48em) {
  .hero__text-box {
    grid-column: 1/span 2;
  }
}
.hero .heading-primary {
  text-align: center;
  padding: 0 9.5rem;
  margin-bottom: 3.4rem;
}
@media (max-width: 48em) {
  .hero .heading-primary {
    padding: 0 22rem;
    margin-bottom: 2.4rem;
  }
}
.hero__description {
  text-align: center;
  line-height: 2.6rem;
  color: var(--text-gray-blue);
  line-height: 2.6rem;
  padding: 0 6rem;
  margin-bottom: 3.2rem;
}
@media (max-width: 48em) {
  .hero__description {
    padding: 0 13.5rem;
  }
}
.hero .btn-download {
  margin-right: 1.6rem;
  background-color: var(--primary);
  padding: 1.6rem 4rem;
}
.hero .btn-download:hover {
  background-color: var(--primary-light);
}
.hero .btn-download:hover .version {
  color: var(--primary);
}
.hero .version {
  margin-left: 0.4rem;
  color: var(--primary-light);
  transition: all 300ms ease-out;
}
.hero .btn-nav {
  background-color: var(--secondary);
}
.hero .btn-nav:hover, .hero .btn-nav:active {
  background-color: var(--secondary-light);
}

.separator {
  display: flex;
  justify-content: center;
}
.separator__number {
  width: 5.6rem;
  height: 5.6rem;
  border: 1px solid var(--text-gray-blue-light);
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--background-white);
  font-size: 1.6rem;
  font-weight: 900;
  line-height: 2.6rem;
  color: var(--text-gray-blue);
  position: relative;
  margin: 19.4rem 0 6.4rem;
}
@media (max-width: 48em) {
  .separator__number {
    margin: 16.4rem 0 6.7rem;
  }
}
.separator__number::after {
  content: "";
  position: absolute;
  top: calc(-100% - 3rem);
  width: 1px;
  height: 8.4rem;
  background-color: var(--text-gray-blue-light);
}
.separator__number--smaller {
  margin-top: 15.6rem;
  margin-bottom: 0rem;
  transform: translateY(50%);
}

.section-featured {
  max-width: 111rem;
  margin: 0 auto;
}

.featured {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.featured__gallery {
  display: flex;
  gap: 3rem;
}
@media (max-width: 48em) {
  .featured__gallery {
    gap: 1.1rem;
  }
}
.featured__gallery-img {
  width: 25.5rem;
  border-radius: 1.2rem;
}
@media (max-width: 48em) {
  .featured__gallery-img {
    width: 16.4rem;
  }
}
.featured__text-box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin-top: 8rem;
}
@media (max-width: 48em) {
  .featured__text-box {
    margin-top: 4.8rem;
  }
}
.featured .heading-secondary {
  margin-bottom: 3.2rem;
  padding: 0 33rem;
}
@media (max-width: 48em) {
  .featured .heading-secondary {
    padding: 0 16rem;
  }
}
.featured .subheading {
  margin-bottom: 1.6rem;
}
.featured__description {
  color: var(--text-gray-blue-light2);
  line-height: 2.6rem;
  padding: 0 30rem;
}
@media (max-width: 48em) {
  .featured__description {
    padding: 0 9rem;
    font-size: 1.6rem;
  }
}

.footer {
  background-image: linear-gradient(rgba(77, 150, 168, 0.9), rgba(77, 150, 168, 0.9)), url(/assets/desktop/image-footer.jpg);
  background-repeat: no-repeat;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 11.6rem 16.5rem 10.4rem 16.5rem;
}
@media (max-width: 48em) {
  .footer {
    background-image: linear-gradient(rgba(77, 150, 168, 0.9), rgba(77, 150, 168, 0.9)), url(/assets/tablet/image-footer.jpg);
    padding: 9.2rem 9.8rem 6.6rem 9.7rem;
  }
}
.footer__text-box {
  display: grid;
  grid-template-columns: 1.5fr 1.5fr 1fr;
  align-items: center;
  justify-content: center;
}
@media (max-width: 48em) {
  .footer__text-box {
    grid-template-columns: 1fr;
  }
}
.footer__description {
  color: var(--white);
  line-height: 2.6rem;
  padding: 0 1rem 0 6rem;
}
@media (max-width: 48em) {
  .footer__description {
    text-align: center;
    padding: 0rem;
    margin-bottom: 4rem;
  }
}
.footer .btn-download-purple {
  background-color: var(--secondary);
  justify-self: end;
  align-self: center;
}
@media (max-width: 48em) {
  .footer .btn-download-purple {
    justify-self: center;
    align-self: center;
  }
}
.footer .btn-download-purple:hover {
  background-color: var(--secondary-light);
}
.footer .btn-download-purple:hover .version-purple {
  color: var(--secondary);
}
.footer .version-purple {
  margin-left: 0.4rem;
  color: var(--secondary-light2);
}